import React from "react"
import BaseTwo from "../Components/Base/BaseTwo"
import DosDonts from "../Components/DashboardDosDonts/DosDonts"
import { getUser } from "../Helpers"

const Header = () => {
  return(
    <div className="mt-10">
      <h2 className="text-white text-2xl">
        Namaste, <strong>{getUser().firstName}</strong>
      </h2>
      <p className="text-white text-sm pt-1">
        Hope you are having a nice day!
      </p>
    </div>
  )
}

const Dashboard = () => {

    const dosData = [
        {
          "text": "Follow the rules and regulations set by the college or university regarding behavior and conduct on the bus."
        },
        {
          "text": "Respect the driver and other passengers by maintaining appropriate behavior and refraining from disruptive actions."
        },
        {
          "text": "Arrive at the bus stop on time to ensure you do not miss the bus."
        },
        {
          "text": "Use headphones when listening to music or watching videos to avoid disturbing others."
        },
        {
          "text": "Keep the bus clean by disposing of trash properly and not littering."
        },
        {
          "text": "Offer your seat to elderly, disabled, or pregnant passengers if necessary."
        },
        {
          "text": "Keep personal belongings contained and out of the aisles to prevent trip hazards."
        },
        {
          "text": "Report any safety concerns or issues to the bus driver or relevant authorities promptly."
        }
    ]
    const dontsData = [
          {
            "text": "Do not engage in loud or disruptive behavior that may disturb other passengers or the driver."
          },
          {
            "text": "Avoid eating messy or strong-smelling foods on the bus to maintain a pleasant environment for everyone."
          },
          {
            "text": "Do not vandalize or damage the bus or its facilities."
          },
          {
            "text": "Refrain from putting feet or belongings on the seats, as this can damage them and inconvenience other passengers."
          },
          {
            "text": "Do not block the aisles or doorways when entering or exiting the bus."
          },
          {
            "text": "Avoid bringing pets or animals onto the bus unless they are service animals."
          },
          {
            "text": "Do not distract the driver while the bus is in motion, as it can compromise everyone's safety."
          },
          {
            "text": "Refrain from engaging in any illegal activities or behaviors while on the bus."
          }

    ]
    
    return(
        <BaseTwo header={<Header />} style="bg-white rounded-t-2xl">
            <div className="flex flex-col items-start">
                <DosDonts title="Do's" data={dosData} />
                <DosDonts title="Dont's" data={dontsData} />
            </div>
        </BaseTwo>
    )
}

export default Dashboard